import React from 'react'
import { NavLink } from 'react-router-dom'
import './Button.css'



function Button({ text, className, path, to }) {
    if (to) {
        return (
            <>
                <div>
                    <a className={className} rel="noreferrer" target="_blank" href={to} >{text}</a>
                </div>
            </>
        )
    }

    return (
        <>
            <div>
                <NavLink className={className} to={path}>{text}</NavLink>
            </div>
        </>
    )
}

export default Button
